import { on, off, throttle, isInViewport } from './modules/fn.mjs';
import { EffectCreative } from './modules/swiper-creative-effect.mjs';
import { EffectCircleRotate } from './modules/swiper-circle-rotate-effect.mjs';

(function(window, document) {
	document.getElementById('header').classList.add('transparent'); // Header 改為透明設定
	document.documentElement.classList.add('page-index'); // Header 改為透明設定

	// Use UAParser.js
	// https://github.com/faisalman/ua-parser-js
	let parser = null;
	let isSafari = false;
	let isMobile = false;
	on(window, 'load:origin', function() {
		parser = new UAParser();
		isMobile = parser.getDevice().model ? true : false;
		isSafari = parser.getBrowser().name === 'Safari';
	});

	/* ---------------------------------------- [START] 整頁 Scroll 監測 (Before) */
	var pageScrollCheckList = []; // 紀錄物件
	var pageScrollAdd = function (selector, buildFunction) {
		var el = document.querySelector(selector);
		if (el === null) {
			return false;
		}
		pageScrollCheckList.push({
			build: false,
			el: el,
			fun: buildFunction,
		});

		// Start Add Element
		var i = pageScrollCheckList.length - 1;
		if (isInViewport(pageScrollCheckList[i].el)) {
			pageScrollCheckList[i].fun();
			pageScrollCheckList[i].build = true;
			pageScrollClean();
		}
	};
	var pageScrollClean = function () {
		pageScrollCheckList = Array.prototype.filter.call(pageScrollCheckList, function (item) {
			return item.build === false;
		});
	};
	/* ---------------------------------------- [END] 整頁 Scroll 監測 (Before) */
	
	/* ---------------------------------------- [START] Swiper Tool */
	on(window, 'load:origin', function() {
		var components = [
			EffectCreative,
			EffectCircleRotate
		];
		Swiper.use(components);
	})
	/* ---------------------------------------- [END] Swiper Tool */

	/* ---------------------------------------- [START] Banner Swiper (Key Circle) */
	const indexBannerCarousel = '#index-key-circle-carousel';
	let bannerBuilded = false;

	function bannerBuild() {
		if (bannerBuilded) {
			return false
		}
		bannerBuilded = true;

		const swiperTarget = indexBannerCarousel;
		// const swiperTargetEl = document.querySelector(swiperTarget);
		// const swiperEl = swiperTargetEl.parentElement;
		let swiper = null;

		const autoplayDelay = 5000; // 自動輪播

		const bgEl = document.querySelector('.index-key-vision__deco');

		const titleEl = document.querySelector('.index-key-circle__title');
		const descEl = document.querySelector('.index-key-circle__desc');
		const changeTextBox = function(title, desc) {
			titleEl.innerHTML = title;
			descEl.innerHTML = desc;
		};

		const smallBuild = function() {
			swiper = new Swiper(swiperTarget, {
				slidesPerView: 'auto',
				loop: true,
				loopedSlides: 2,
				autoplay: {
					delay: autoplayDelay,
				},
				lazy: {
					loadPrevNext: true,
					loadPrevNextAmount: 3
				},
				preloadImages: false,
				slideToClickedSlide: true,
				centeredSlides: true,
	
				effect: 'creative',
				creativeEffect: {
					// 圓形大小（由左至右）：50 87 127 61
					// 127 Active
					progressMultiplier: 1,
					prev: {
						// Active 左邊項目圓形：
						// Scale: 87 / 127(Active) = 0.685
						translate: ['-110%', '-20%', 0],
						scale: 0.65,
					},
					next: {
						// Active 右邊項目圓形：
						// scale: 61 / 127(Active) = 0.480
						translate: ['88%', '-40%', 0],
						scale: 0.48,
					},
					deep: true, // 是否層遞影響
					rate: 0.65, // 以幾倍的方式遞增/遞減
				},
				on: {
					slideChange () {
						const sw = this;
						const currentSlide = sw.slides[sw.activeIndex];
						const bg = currentSlide.dataset['deco'] || null;
						const title = currentSlide.dataset['title'] || '';
						const desc = currentSlide.dataset['desc'] || '';

						if (bg) {
							bgEl.style.backgroundImage = `url(${bg})`;
						} else {
							bgEl.style.backgroundImage = '';
						}

						changeTextBox(title, desc);
					}
				}
			});
		}

		const largeSwiperBuild = function() {
			let bodyWidthHalf = (document.body || document.querySelector('body')).clientWidth / 2;
			const indexBanner = document.querySelector('.index-key-circle');

			let area = null;

			const mouseleave = function() {
				indexBanner.dataset.area = '';
				area = null;
			}

			const mousemove = function(e) {
				if (e.pageX < bodyWidthHalf) {
					area = 'left';
					indexBanner.dataset.area = 'left';
				} else {
					area = 'right';
					indexBanner.dataset.area = 'right';
				}
			}

			const clickHandler = function(e) {
				if (!$(e.target).parents('.index-key-circle__item').length) {
					if (area === 'left') {
						// Prev
						// console.log('[Prev]', 'data-area', indexBanner.dataset.area, 'area', area);
						swiper.slidePrev();
					} else {
						// Next
						// console.log('[Next]', 'data-area', indexBanner.dataset.area, 'area', area);
						swiper.slideNext();
					}
				}
			}

			const resizeHandler = function() {
				bodyWidthHalf = (document.body || document.querySelector('body')).clientWidth / 2;
			}

			const eventOn = function() {
				if (!isMobile) {
					window.addEventListener('resize', resizeHandler);
					indexBanner.addEventListener('mouseleave', mouseleave);
					indexBanner.addEventListener('mousemove', mousemove);
					indexBanner.classList.add('mouse-init');
				}
				indexBanner.addEventListener('click', clickHandler);
			}
			
			const eventOff = function() {
				window.removeEventListener('resize', resizeHandler);
				indexBanner.removeEventListener('mouseleave', mouseleave);
				indexBanner.removeEventListener('mousemove', mousemove);
				indexBanner.removeEventListener('click', clickHandler);
				indexBanner.classList.remove('mouse-init');
			}

			swiper = new Swiper(swiperTarget, {
				slidesPerView: 'auto',
				loop: true,
				loopedSlides: 4,
				centeredSlides: true, // if use `loop` + `slidesPerView:'auto'`, must use this API, or it will has bug
				slideToClickedSlide: true,
				simulateTouch: false, // 關閉滑鼠拖曳
				autoplay: {
					delay: autoplayDelay,
					disableOnInteraction: false
				},
				lazy: {
					loadPrevNext: true,
					loadPrevNextAmount: 5
				},
				effect: 'circle-rotate',
				circleRotateEffect: {
					start: 305,
					per: 15,
					next: {
						angle: [-60, -76, -87, -110],
						scale: [0.75, 0.5, 0.33, 0],
					},
					prev: {
						angle: [40],
						scale: [0],
					}
				},
				breakpoint: {
					1600: {
						circleRotateEffect: {
							start: 305,
							per: 15,
							next: {
								angle: [-57, -73, -84, -110],
								// angle: [-55, -70, -80, -105],
								scale: [0.75, 0.5, 0.33, 0],
							},
							prev: {
								angle: [40],
								scale: [0],
							}
						},
					}
				},
				on: {
					init: function() {
						eventOn();

						// Safari 在 transform 300ms 的時候會閃爍，會很消耗效能，但只能出此下策
						if (isSafari) {
							this.$el[0].classList.add('fixed-will-change');
						}
					},
					beforeDestroy: function() {
						eventOff();
					},
					slideChange () {
						const currentSlide = this.slides[this.activeIndex];
						const title = currentSlide.dataset['title'] || '';
						const desc = currentSlide.dataset['desc'] || '';
						changeTextBox(title, desc);
					},
					slideChangeTransitionStart: function slideChangeTransitionStart() {
						const { slides } = this;

						// Safari 在 transform 300ms 的時候會閃爍，會很消耗效能，但只能出此下策
						for (let i = 0; i < slides.length; i++) {
							const $slideEl = slides.eq(i);
							$slideEl.css('will-change', 'transform');
						}
					},
					slideChangeTransitionEnd: function slideChangeTransitionEnd() {
						setTimeout( () => {
							const { slides } = this;

							for (let i = 0; i < slides.length; i++) {
								const $slideEl = slides.eq(i);
								$slideEl.css('will-change', '');
							}
						}, 100)
					}
				}
			});
		}

		const breakpoint = window.matchMedia('(min-width: 1024px)');
		const breakpointChecker = function () {
			if (swiper !== null) {
				swiper.destroy(true, true);
				swiper = null;
			}

			if (breakpoint.matches) {
				// Large
				largeSwiperBuild();
			} else {
				// Small + Medium
				smallBuild();
			}
		}

		// Start
		breakpointChecker();
		breakpoint.addListener(breakpointChecker);
	}
	// on( window, 'load', function () {
	// 	pageScrollAdd(indexBannerCarousel, bannerBuild);
	// });

	// 配合套版，給予可呼叫之function
	window.indexKeyCircleBuild = function() {
		console.log('indexKeyCircleBuild')
		pageScrollAdd(indexBannerCarousel, bannerBuild);
	};
	/* ---------------------------------------- [END] Banner Swiper (Key Circle) */

	/* ---------------------------------------- [START] Alert Marquee */
	// Alert Marquee （跑馬燈）
	let marqueeBuilded = false;
	function marqueeBuild() {
		if (marqueeBuilded) {
			return false
		}
		marqueeBuilded = true;
		console.log('indexMarqueeBuild');

		$('.marquee').marquee({
			speed: 20,
			gap: 0,
			delayBeforeStart: 0,
			duplicated: true,
			startVisible: true,
		});
	}
	// on( window, 'load', marqueeBuild);

	// 配合套版，給予可呼叫之function
	window.indexMarqueeBuild = marqueeBuild;
	/* ---------------------------------------- [END] Alert Marquee */

	
	/* ---------------------------------------- [START] Activity Swiper (Banner) */
	const indexActivityCarousel = '#index-banner-carousel';

	let activityBuilded = false;
	function activityBuild() {
		if (activityBuilded) {
			return false
		}
		activityBuilded = true;

		const swiperTarget = indexActivityCarousel;
		const swiperEl = document.querySelector(swiperTarget).parentElement;
		const swiperNextEl = swiperEl.querySelector('.swiper-button-next');
		const swiperPrevEl = swiperEl.querySelector('.swiper-button-prev');
		const swiperPageEl = swiperEl.querySelector('.swiper-pagination');

		let swiper = null;

		const buildSwiperMobile = function() {
			swiper = new Swiper(swiperTarget, {
				spaceBetween: 11,
				centeredSlides: true,
				slidesPerView: 'auto',
				loop: true,
				preloadImages: false,
				lazy: {
					loadPrevNext: true
				},
				pagination: {
					el: swiperPageEl,
					clickable: true,
					dynamicBullets: true,
					dynamicMainBullets: 3,
				},
				navigation: {
					nextEl: swiperNextEl,
					prevEl: swiperPrevEl
				},
				breakpoints: {
					640: {
						spaceBetween: 26
					}
				}
			});
		}

		const buildSwiperLarge = function() {
			swiper = new Swiper(swiperTarget, {
				spaceBetween: 26,
				grabCursor: true,
				slidesPerView: 2,
				slidesPerGroup: 2,
				loop: true,
				preloadImages: false,
				lazy: {
					loadPrevNext: true
				},
				pagination: {
					el: swiperPageEl,
					clickable: true,
					dynamicBullets: true,
					dynamicMainBullets: 3,
				},
				navigation: {
					nextEl: swiperNextEl,
					prevEl: swiperPrevEl
				},
				breakpoints: {
					1200: {
						slidesPerView: 3,
						slidesPerGroup: 3,
					}
				}
			});
		}

		const breakpoint = window.matchMedia('(min-width: 1024px)');
		const breakpointChecker = function() {

			if (swiper !== null) {
				swiper.destroy(true, true);
				swiper = null;
			}
			
			if(breakpoint.matches) {
				// Large
				buildSwiperLarge();
			} else {
				// Small + Medium
				buildSwiperMobile();
			}
		};

		breakpointChecker();
		breakpoint.addListener(breakpointChecker);
	}
	// on( window, 'load', function () {
	// 	if (document.querySelector(indexActivityCarousel) !== null) {
	// 		pageScrollAdd(indexActivityCarousel, activityBuild);
	// 	}
	// });

	// 配合套版，給予可呼叫之function
	window.indexBannerBuild = function() {
		console.log('indexBannerBuild');
		if (document.querySelector(indexActivityCarousel) !== null) {
			pageScrollAdd(indexActivityCarousel, activityBuild);
		}
	};
	/* ---------------------------------------- [END] Activity Swiper (Banner) */

	/* ---------------------------------------- [START] Store Swiper */
	const indexStoreCarousel = '#index-store-carousel';
	
	let storeBuilded = false;
	function storeBuild() {
		if (storeBuilded) {
			return false
		}
		storeBuilded = true;

		let swiper = null;
		const swiperTarget = indexStoreCarousel;
		const swiperEl = document.querySelector(swiperTarget).parentElement;

		const swiperBuild = function () {
			const swiperNextEl = swiperEl.querySelector('.swiper-button-next');
			const swiperPrevEl = swiperEl.querySelector('.swiper-button-prev');
	
			swiper = new Swiper(swiperTarget, {
				slidesPerView: 'auto',
				slidesPerGroup: 6,
				preloadImages: false,
				lazy: {
					loadPrevNext: true,
					loadPrevNextAmount: 8
				},
				navigation: {
					nextEl: swiperNextEl,
					prevEl: swiperPrevEl,
				},
				breakpoints: {
					// when window width is >= 1320px
					1320: {
						slidesPerGroup: 7,
					}
				},
			});
		}

		const swiperDestroy = function() {
			if (swiper !== null) {
				swiper.destroy(true, true);
				swiper = null;
			}
		}

		const breakpoint = window.matchMedia('(min-width: 1024px)');
		const breakpointChecker = function() {
			if (breakpoint.matches) {
				// Large+
				swiperBuild();
			} else {
				// Small + Medium
				swiperDestroy();
			}
		}

		// Start
		breakpointChecker();
		breakpoint.addListener(breakpointChecker);
	}
	// on( window, 'load', function () {
	// 	if (document.querySelector(indexStoreCarousel) !== null) {
	// 		pageScrollAdd(indexStoreCarousel, storeBuild);
	// 	}
	// });

	// 配合套版，給予可呼叫之function
	window.indexStoreBuild = function() {
		console.log('indexStoreBuild');
		if (document.querySelector(indexStoreCarousel) !== null) {
			pageScrollAdd(indexStoreCarousel, storeBuild);
		}
	};
	/* ---------------------------------------- [END] Store Swiper */

	/* ---------------------------------------- [START] 整頁 Scroll 監測 (After) */
	var pageScrollThrottle = null;
	var pageScrollHandler = function () {
		if (pageScrollCheckList.length === 0) {
			return false;
		}

		for (var i = 0; i < pageScrollCheckList.length; i++) {
			if (isInViewport(pageScrollCheckList[i].el)) {
				pageScrollCheckList[i].fun();
				pageScrollCheckList[i].build = true;
			}
		}
		pageScrollClean();
		if (pageScrollCheckList.length === 0) {
			off(window, 'scroll', pageScrollThrottle);
		}
	};

	on(window, 'load', function () {
		pageScrollThrottle = throttle(pageScrollHandler, 50, 1000); // 節流作用
		on(window, 'scroll', pageScrollThrottle);
		pageScrollHandler();
	});
	/* ---------------------------------------- [END] 整頁 Scroll 監測 (After) */

})(window, document);
